              </div>
              <div class="right">
                <div class="apply">申请加入</div>
              </div>
            </div>
            <div class="bottomLine">
              <div></div>
            </div>
          </div>
          <div class="item">
            <div class="top">
              <div class="left">
                <img :src="homeIcon" alt />
              </div>
              <div class="right">
                <div
                  class="city"
                >【{{detail.cityName}}】【{{detail.areaName}}-{{detail.buildName}}-{{detail.showRoomName}}】</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom-btn" v-if="detail">
      <div class="disagree" @click="check(2)">拒绝</div>
      <div class="agree" @click="check(1)">接受</div>
    </div>
    <!-- <div class="overlayView" v-if="showSheet" @click="showSheet=false;showList=false">
      <div class="dialogView">
        <div class="dialogTitle">请选择申请人身份标签</div>
