        </div>
      </div>
    </div>-->
    <van-popup v-model="showSheet" position="bottom">
      <van-picker
        title="请选择申请人身份标签"
        show-toolbar
        :columns="tagArr"
        value-key="tagName"
        @confirm="onConfirm"
        @cancel="onCancel"
      />
    </van-popup>
  </div>
</template>

<script>
import {
  getDetailService,
  getIdentityTagList,
} from "../../service/verifyApplyService";
import homeIcon from "@/assets/mine/fangjian.png";
import defaultIcon from "@/assets/mine/user_Default_avata.png";
import PageHeader from "./PageHeader";
export default {
  name: "TabBar",
  components: {
    PageHeader,
  },
  data() {
    return {
      detail: null,
      homeIcon: homeIcon,
      defaultIcon: defaultIcon,
      icon: {
        xwjlogo: require("../../assets/share/shareLogo.png"),
      },
      showSheet: false,
      showList: false,
      selctedIdenti: "全部商品",
      tagArr: [],
    };
  },
  computed: {
    arrowDirection() {
      return this.showList ? "up" : "down";
    },
  },
  mounted() {
    let param = this.$route.params;
    this.uuid = param.uuid.split("=")[1];
    this.getDetail();
    this.getIdentiTags();
    this.androidShareOpenApp("myfamily"); //android 拉起APP
  },
  methods: {
    getDetail() {
      getDetailService({
        uuid: this.uuid, // this.getQueryVariable("uuid")
      }).then(
        (res) => {
          console.log(res);
          if (res.resultCode == 0) {
            this.detail = res.data;
          } else {
            this.$toast(res.msg);
          }
        },
        (error) => {
          // this.$toast(error);
        }
      );
    },

    /**
     * 获取url参数
     */
    getQueryVariable(variable) {
      var query = window.location.search.substring(1);
      console.log(query);
      var vars = query.split("&");
      for (var i = 0; i < vars.length; i++) {
        var pair = vars[i].split("=");
        if (pair[0] == variable) {
          return pair[1];
        }
      }
      return false;
    },
    getIdentiTags() {
      let param = {
        tagStatus: 1,
        page: 1,
        pageSize: 100,
      };
      getIdentityTagList(param)
        .then((res) => {
          if (res.resultCode == 0) {
            this.tagArr = res.data.list;
          } else {
            this.$toast(res.msg);
          }
        })
        .catch((error) => {
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /**
     * 跳转到验证手机号页面
     */
    check(status) {
      if (status == 1) {
        this.showSheet = true;
        return;
      }
      if (Object.keys(this.detail).length == 0) return;
      this.$router.push({
        name: "verifyPhone",
        query: { data: JSON.stringify(this.detail), type: status },
      });
    },
    openApp() {
      this.shareOpenApp("myfamily");
    },
    onConfirm(item) {
      this.selctedIdenti = item.tagName;
      this.showSheet = false;
      this.$router.push({
        name: "verifyPhone",
        query: {
          data: JSON.stringify(this.detail),
          type: 1,
          identify: JSON.stringify({
            tagName: item.tagName,
            tagCode: item.tagCode,
          }),
        },
      });
    },
    onCancel() {
      this.showSheet = false;
    },
  },
};
</script>

